import axios from 'axios';

// const commentsURL = 'https://6352305e9d64d7c71310822d.mockapi.io/comments';
// Post
// This below is the postURL before modification (without text features)
// const postURL = 'https://634a238533bb42dca4fed106.mockapi.io/posts';
// This below is the postURL after modification and is used for text features
// const postURL = 'https://634a238533bb42dca4fed106.mockapi.io/Posts';
const postURL = 'http://localhost:8080';
export const userId = localStorage.getItem('myUserID');

export const getCurrentUserProfile = async () => {
  try {
    const response = await axios.get(`http://localhost:8080/users/${userId}`);
    return response.data.data;
  } catch (err) {
    console.error(err);
  }
};

// following = followers

export const getFollowersbyId = async (id) => {
  try {
    const response = await axios.get(`http://localhost:8080/users/${id}/followers`);
    return response.data;
  } catch (err) {
    console.error(err);
  }
};

export const getFolloweesbyId = async (id) => {
  try {
    const response = await axios.get(`http://localhost:8080/users/${id}/followees`);
    return response.data;
  } catch (err) {
    console.error(err);
  }
};

// friend recommendation

export const getRecommendationById = async (id) => {
  try {
    const response = await axios.get(`http://localhost:8080/recommendation/${id}`);
    return response.data;
  } catch (err) {
    console.error(err);
  }
};

// get user infomation by given id
export const getUserById = async (id) => {
  try {
    const response = await axios.get(`http://localhost:8080/users/${id}`);
    return response.data.data;
  } catch (err) {
    console.error(err);
  }
};

// get all the users information
export const getAllUser = async () => {
  try {
    const response = await axios.get('http://localhost:8080/users');
    return response.data.data;
  } catch (err) {
    console.error(err);
  }
};

// update userInfo
export const updateUserInfo = async (id, userObject) => {
  try {
    const response = await axios.put(`http://localhost:8080/users/${id}`, userObject);
    return response.data.data;
  } catch (err) {
    console.error(err);
  }
};

// Post

// Get the feed (many posts)
// Create a fetch function to make the GET request to the API
export const getAllPosts = async () => {
  try {
    const response = await axios.get(`${postURL}/posts`);
    return response.data;
  } catch (err) {
    console.error(err);
  }
};

// Get a post by the post id
// Create a fetch function to make the get request to the API
export const getPostById = async (id) => {
  try {
    const response = await axios.get(`${postURL}/post/${id}`);
    return response.data;
  } catch (err) {
    console.error(err);
  }
};

// Create a post
// Create a fetch function to make the POST request to the API
export const createPost = async (postObject) => {
  try {
    const response = await axios.post(`${postURL}/post/`, {
      photo: postObject.photo,
      userId: postObject.userId,
      likes: postObject.likes,
      comment: postObject.comment,
      profilePicture: postObject.profilePicture,
      username: postObject.username,
      desc: postObject.desc,
    });
    // return the data with the id of the post
    return response.data;
  } catch (err) {
    console.error(err);
  }
};

// Update likes (the array of userId) by a given Id
// Create a fetch function to make the PUT request to the API
export const updateLike = async (postObject, id) => {
  try {
    if (!postObject.likes?.includes(parseInt(userId, 10))) {
      postObject.likes?.push(parseInt(userId, 10));
    } else {
      postObject.likes?.pop();
    }
    const likesUpdates = { likes: postObject.likes };
    // const likes = { likes: `${!postObject.likes.includes(userId) ?
    // postObject.likes.push(userId) : postObject.likes.pop(userId)}` };
    await axios.put(`${postURL}/${id}`, likesUpdates);
    // return response.data;
    // return the data with the id of the student
  } catch (err) {
    console.error(err);
  }
};

// Update post by a given Id
// Create a fetch function to make the PUT request to the API
export const updatePost = async (postObject, id) => {
  try {
    const postUpdates = { photo: postObject.photo, desc: postObject.desc };
    await axios.put(`${postURL}/post/${id}`, postUpdates);
  } catch (err) {
    console.error(err);
  }
};

// Delete a post
// Create a fetch function to make the DELETE request to the API
export const deletePostbyId = (id) => {
  try {
    return axios.delete(`${postURL}/post/${id}`);
  } catch (err) {
    console.error(err);
  }
};

// Login
// Get login userinformation
export const userLogin = async (username, password) => {
  try {
    const response = await axios({
      method: 'post',
      url: 'http://localhost:8080/login',
      responseType: 'json',
      headers: {
        'Content-Type': 'application/json',
      },
      data: {
        username: username,
        password: password,
      },
    });
    return response.data;
  } catch (err) {
    console.error(err);
  }
};

// Registration
// Creat a new user
export const userRegistration = async (name, username, password, confirmPassword) => {
  try {
    const response = await axios({
      method: 'post',
      url: 'http://localhost:8080/registration',
      responseType: 'json',
      headers: {
        'Content-Type': 'application/json',
      },
      data: {
        name: name,
        username: username,
        password: password,
        confirmpassword: confirmPassword,
      },
    });
    return response.data;
  } catch (err) {
    console.error(err);
  }
};

// Comment
// Get comments by given postId
export const getCommentsByPostId = async (id) => {
  try {
    const response = await axios.get(`http://localhost:8080/comments/${id}`);
    return response.data;
  } catch (err) {
    console.error(err);
  }
};

export const getCommentById = async (id) => {
  try {
    const response = await axios.get(`http://localhost:8080/comment/${id}`);
    return response.data;
  } catch (err) {
    console.log(err);
  }
};

// Edit a comment
export const updateComment = async (newContent, commentId) => {
  await axios({
    method: 'put',
    url: `http://localhost:8080/comment/${commentId}`,
    responseType: 'json',
    headers: {
      'Content-Type': 'application/json',
    },
    data: {
      commentId,
      content: newContent,
    },
  })
    .then((response) => (response))
    .catch((err) => (err));
};

// Delete a comment by given commentId
export const deleteCommentById = async (id) => {
  try {
    const response = await axios.delete(
      `http://localhost:8080/comment/${id}`,
    );
    return response.data;
  } catch (err) {
    console.error(err);
  }
};
// Edit the comment also in the comments endpoint
export const editCommentInComments = async (newComments, postId) => {
  await axios({
    method: 'put',
    url: `http://localhost:8080/comments/${postId}`,
    responseType: 'json',
    headers: {
      'Content-Type': 'application/json',
    },
    data: {
      comment: newComments,
    },
  })
    .then((response) => (response))
    .catch((err) => (err));
};

// Add a comment by given user object
export const addComment = async (userID, userName, content, id) => {
  try {
    const response = await axios({
      method: 'post',
      url: 'http://localhost:8080/comment',
      responseType: 'json',
      headers: {
        'Content-Type': 'application/json',
      },
      data: {
        userId: userID,
        content,
        postId: id,
        name: userName,
      },
    });
    return response.data;
  } catch (err) {
    console.error(err);
  }
};
