/* eslint-disable no-underscore-dangle */
import './feed.css';
// import useState and useEffect
import React, { useState, useEffect } from 'react';
// import from api
import { getAllPosts, getUserById } from '../../mockAPI/mockAPI';
// import from components
import Post from '../post/post';
import Upload from '../upload/upload';

export default function Feed() {
  // Create a state to hold the feed (many posts) and the function to update it.
  const [posts, setPosts] = useState([]);

  //  Call the function defined in the api in the useEffect hook
  useEffect(() => {
    async function fetchPosts() {
      const data = await getAllPosts();
      setPosts(data);
    }
    fetchPosts();
  }, []);

  // get the current viewer
  const userId = localStorage.getItem('myUserID');
  const [currUser, setCurrUser] = useState(null);
  useEffect(() => {
    async function getUser(id) {
      const data = await getUserById(id);
      setCurrUser(data);
    }
    getUser(userId);
  }, []);

  return (
    <div className="feedContainer">
      <div className="feedWrapper"> </div>
      <Upload viewer={currUser} />
      {posts.map((p) => (
        <Post key={p._id} post={p} viewer={currUser} />
      ))}
    </div>
  );
}
